/* NOTE! This is the soon-to-be *deprecated* implementation used for rendering Draft
 * Find the new files in src/render/text/draft
 */
import { DraftInlineStyle, ContentBlock } from "draft-js"
import * as React from "react"
import { fontStore } from "../fonts/fontStore"
import { TypefaceSourceNames } from "../fonts/types"
import { ConvertColor } from "../types/Color"
import { isFiniteNumber } from "../utils/isFiniteNumber"
import { isString, isUndefined } from "util"
import { TextDecoration, TextTransform } from "../text/draft"
import { getStyleForTypefaceOrSelector } from "../text"

// Style queries and changes

/** @internal */
export const draftStyleDefinitions: DraftStyles = {
    font: {
        prefix: "FONT:",
        default: fontStore.defaultFont.selector,
        setCSS: getStyleForTypefaceOrSelector,
        fromCSS: getFontStyleStringFromCSS,
    },
    color: {
        prefix: "COLOR:",
        default: "rgb(0, 0, 0)",
        setCSS: (value, css) => (css.WebkitTextFillColor = value),
        fromCSS: css => {
            let color
            if (css.webkitTextFillColor !== null) {
                color = css.webkitTextFillColor
            }
            if (css.color !== null) {
                color = css.color
            }
            if (color) {
                return ConvertColor.toRgbString(color)
            }
        },
    },
    size: {
        prefix: "SIZE:",
        default: 16,
        setCSS: (value, css) => (css.fontSize = `${value}px`),
        fromCSS: css => getCSSFloatString(css, "fontSize"),
    },
    letterSpacing: {
        prefix: "LETTERSPACING:",
        default: 0,
        setCSS: (value, css) => (css.letterSpacing = `${value}px`),
        fromCSS: css => getCSSFloatString(css, "letterSpacing", 1),
    },
    lineHeight: {
        prefix: "LINEHEIGHT:",
        default: 1.2,
        setCSS: (value, css) => {
            // Some documents might be saved with a NaN value for line height.
            // If those are found, we are making sure they fall back to their default value on the canvas.
            if (value === undefined || Number.isNaN(parseFloat(value.toString()))) {
                css.lineHeight = `${draftStyleDefinitions.lineHeight.default}`
            } else {
                css.lineHeight = `${value}`
            }
        },
        fromCSS: css => safeLineHeight(css),
    },
    align: {
        prefix: "ALIGN:",
        // Not handled by the style function
    },
    textTransform: {
        prefix: "TEXTTRANSFORM:",
        default: undefined,
        setCSS: (value, css) => {
            // clear the css if it's not a meaningful value
            css.textTransform = isValidTextTransform(value) ? value : undefined
        },
        fromCSS: css => {
            return css.textTransform
        },
    },
    textDecoration: {
        prefix: "TEXTDECORATION:",
        default: undefined,
        setCSS: (value, css) => {
            css.textDecoration = isValidTextDecoration(value) ? value : undefined
        },
        fromCSS: css => css.textDecoration,
    },
}

const getCSSFloatString = (
    css: CSSStyleDeclaration,
    key: keyof CSSStyleDeclaration,
    fractionDigits?: number
): string | undefined => {
    const value = css[key]

    let result = NaN
    if (typeof value === "number") {
        result = value
    } else if (typeof value === "string") {
        result = parseFloat(value)
    } else {
        return
    }

    if (isNaN(result)) {
        return
    }
    return fractionDigits === undefined ? `${result}` : result.toFixed(fractionDigits)
}

function isStyleHandler<T = any>(object: any): object is StyleHandler<T> {
    return object.setCSS !== undefined
}

function safeLineHeight(css: CSSStyleDeclaration): string | undefined {
    const lineHeight = css.lineHeight
    if (lineHeight === null) return
    if (Number.isNaN(parseFloat(lineHeight))) return
    return lineHeight
}

/** @internal */
export const draftStyleFunction = (styles: DraftInlineStyle, block?: ContentBlock, styleSelection: boolean = true) => {
    const CSS: React.CSSProperties = {
        tabSize: 4,
    }
    for (const styleType in draftStyleDefinitions) {
        const styleHandler = draftStyleDefinitions[styleType]
        if (isStyleHandler(styleHandler)) {
            styleHandler.setCSS(styleHandler.default, CSS)
        }
    }

    styles.forEach((style: string) => {
        if (style === "BOLD") {
            if (isFiniteNumber(CSS.fontWeight)) {
                CSS.fontWeight = Math.max(
                    (CSS.fontWeight as number) + 300,
                    900
                ) as any /* Assume we have a correct number */
            } else {
                CSS.fontWeight = "bold"
            }
        } else if (style === "ITALIC") {
            CSS.fontStyle = "italic"
        } else if (style === "SELECTION" && styleSelection) {
            CSS.backgroundColor = "rgba(128,128,128,0.33)"
        } else {
            for (const styleType in draftStyleDefinitions) {
                const styleHandler = draftStyleDefinitions[styleType]
                if (!isStyleHandler(styleHandler)) {
                    continue
                }
                if (style.startsWith(styleHandler.prefix)) {
                    styleHandler.setCSS(style.slice(styleHandler.prefix.length), CSS)
                    break
                }
            }
        }
    })

    return CSS
}

export function getFontStyleStringFromCSS(css: CSSStyleDeclaration): string | undefined {
    if (css.fontFamily === null) {
        return
    }

    const familyMembers = css.fontFamily.split(/['"]?, ['"]?/)
    if (familyMembers.length === 0) {
        return
    }
    if (familyMembers.length > 1) {
        familyMembers.pop() // Remove fallback
    }
    familyMembers[0] = familyMembers[0].replace(/^['"]/, "")

    let selector = familyMembers.join("|")
    // Note: this is an assumption, because copying from another document with a missing font
    // might also end up here, thats why well keep it intact if we cant find it.
    let font = fontStore.getFontBySelector(selector)
    if (!font) {
        familyMembers.pop()
        const possibleSelector = familyMembers.join("|")
        if (fontStore.getFontBySelector(possibleSelector)) {
            selector = possibleSelector
        }
    }

    // Resolve aliases
    const aliasSelector = fontStore.local.getTypefaceAliasBySelector(selector)
    if (aliasSelector) {
        selector = aliasSelector
    }

    // Clear font weight, if we have a selector this is already set and matches the weight
    // NOTE: This is a hack! It modifies the parameter passed in because it knows that font weight will
    // be processed after getting the font.
    font = fontStore.getFontBySelector(selector)
    if (font) {
        const weight = font.weight
        if (weight && `${weight}` === css.fontWeight) {
            css.fontWeight = "normal"
        }
    }

    return selector
}
/**
 * A set of functions which define how to apply a specific attribute of text
 * styling (font, color, size, etc.) into CSS and from CSS
 *
 * @internal
 */
export interface StyleHandler<T> {
    prefix: string
    default: T
    setCSS: (value: string | T, css: React.CSSProperties) => void
    fromCSS: (css: CSSStyleDeclaration) => string | undefined
}

/** @internal */
interface DraftStyles {
    font: StyleHandler<string>
    color: StyleHandler<string>
    size: StyleHandler<number>
    letterSpacing: StyleHandler<number>
    lineHeight: StyleHandler<string | number>
    align: { prefix: string }
    textTransform: StyleHandler<TextTransform>
    textDecoration: StyleHandler<TextDecoration>
}
const validTextTransformValues: TextTransform[] = ["capitalize", "uppercase", "lowercase", undefined]
export function isValidTextTransform(value: unknown): value is TextTransform {
    if (!isString(value) && !isUndefined(value)) return false
    return validTextTransformValues.includes(value as TextTransform)
}

const validTextDecoration = ["underline", "line-through", undefined]
export function isValidTextDecoration(value: unknown): value is TextDecoration {
    if (!isString(value) && !isUndefined(value)) return false
    return validTextDecoration.includes(value)
}
